
.vue-grid-layout {
  margin: 0 -10px 100px;
}
.vue-grid-item {
  border: 1px solid #ddd;
}
.widget {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  overflow: hidden;
}
.widget .widget-header {
    border-bottom: 1px solid #ddd;
    height: 30px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding: 0 7px;
}
.widget .iframe-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.widget .iframe-wrapper {
    overflow: hidden;
    width: 100%;
    height: 100%;
    height: calc(100% - 30px);
    overflow: hidden;
}
.widget .iframe-wrapper iframe {
      width: 100%;
      height: 100%;
      overflow-y: scroll;
}



