
.datatable-options {
  padding: .75rem 1rem !important;
  background-color: #FAFAFC;
  border: 1px solid #DDDDEE;
  border-bottom: 0;
  width: 100% !important;
  margin-top: 10px;
}
.datatable-options input {
    border: 1px solid #ccc;
    background: #fff;
    width: calc(100% - 15px);
    border: 1px solid #ccc;
    padding: 0 7px;
    margin: 0;
}
.table {
  border: 1px solid #DDDDEE;
  width: 100% !important;
  white-space: normal !important;
}
.table tr th, .table tr td {
    text-align: center;
}
.table tr th:first-child, .table tr td:first-child {
      text-align: left;
      width: 100%;
}
.table tr th {
    white-space: nowrap;
}
.table tr td {
    border-right-color: #DDDDEE;
    background-color: #fff;
    border-radius: 0;
}
.table tr:nth-child(even) > td {
    background-color: #FAFAFA;
}
.table thead tr th {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-weight: bold;
    background-color: #FAFAFC;
}
.table thead tr th span {
      vertical-align: middle;
}
.table thead tr th .material-icons {
      vertical-align: middle;
      font-size: 16px;
}
.table thead tr th .material-icons.down::after {
        content: 'arrow_downward';
}
.table thead tr th .material-icons.up::after {
        content: 'arrow_upward';
}
.table thead tr th {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-weight: bold;
    background-color: #FAFAFC;
}
.table thead tr th span {
      vertical-align: middle;
}
.table tbody tr {
    cursor: pointer;
}
.table tbody tr .links {
      white-space: nowrap;
}
.table tbody tr .links .material-icons {
        opacity: .7;
        color: #3f51b5 !important;
}
.table tbody tr .links .material-icons:hover {
          opacity: 1;
}



